.employee-form label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    color:black;
    background-color: white; 
    border: 1px solid black;  
}

.employee-form {
    max-width: 500px;
    margin: auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
}

.employee-form label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

.employee-form input,
.employee-form select {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.employee-form .error {
    color: red;
    font-size: 0.9em;
    margin-top: -8px;
    margin-bottom: 10px;
}

.employee-form .success {
    color: green;
    font-size: 1em;
    margin-top: 10px;
}
